	module.exports = {"data":{"wordpressPost":{"title":"The shocking confessions of a beauty editor","content":"<p><strong>Stephanie Darling</strong> is one of Australia&#8217;s most admired Beauty Editors. More importantly she&#8217;s one of my sweetest friends. She&#8217;s hilarious, big-hearted and what she doesn&#8217;t know about beauty is, quite simply, not worth knowing.</p>\n<p>Here, we chat to the very appropriately-named Mrs Darling about her weekly column in Sunday Life and her newly-released beauty book <a href=\"https://www.dymocks.com.au/book/secrets-of-a-beauty-queen-by-stephanie-darling-9780670079216/#.WNpWgBhh1Bw\">Secrets of a Beauty Queen</a>.</p>\n<p><strong>Firstly, a little bit of background &#8230; how did you get started in this field?</strong><br />\nI got my first job in journalism at the age of 23 at <strong>Vogue Australia</strong> where I was a trainee sub-editor. I couldn’t believe it. The rigorous training I got there, plus being surrounded by oodles of style has made me the journalist and now author I am today.</p>\n<p><strong>How long have you worked in the industry?</strong><br />\nFor over 30 years. Do not do the maths!</p>\n<p><strong>What would surprise us about working in the industry? Well, not me, because I work in it too, but you know, the folk who don&#8217;t?</strong><br />\nI think people would be surprised by what a supportive industry it is. I have made and continue to make lifelong friends. We are always helping each other out with advice and information and giving constructive feedback. I found this especially when I was writing <strong>Secrets of a Beauty Queen</strong>. The input and support I had from the beauty posse was incredible.</p>\n<img class=\"alignnone size-medium wp-image-1387\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104354/md1008Stephanie011-1-200x300.png\" alt=\"\" width=\"200\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104354/md1008Stephanie011-1-200x300.png 200w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104354/md1008Stephanie011-1-768x1151.png 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104354/md1008Stephanie011-1-683x1024.png 683w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104354/md1008Stephanie011-1.png 854w\" sizes=\"(max-width: 200px) 100vw, 200px\" />\n<p><em>Yes &#8230; she looks like this. She really does. </em></p>\n<p><strong>Do you really spend all day every day sipping expensive champagne and getting your nails done? (Please tell me you do!)</strong><br />\nOnly every second day. Just kidding. My column in Sunday Life has a road test element so I am now up to 179 road tests all of them different. This means I get to test drive all sorts of fabulous and sometimes painful procedures, including getting my nails done.</p>\n<p><strong>What is the beauty tip you wish with all your being that the rest of us knew about? Again not me because I know everything &#8211; obvs.</strong><br />\nHow important it is to wear SPF50+ sunscreen every day of your life on your face, décolletage (remember people &#8211; your face ends at your boobs) and on the backs of your hands. You need to do this if you don’t want to end up looking 15 years older than you actually are.</p>\n<p><strong>What is the beauty tip you use regularly that you still find completely surprising?</strong><br />\nWhen I apply khol to the inner rim of my lower lid. I really should do this every day as it is completely rock star. I love black but have also experimented with violet and red!</p>\n<p><strong>What&#8217;s your daily skincare regime look like? Don&#8217;t lie! Remember we know you!</strong><br />\nOk so I am on a new Rationale regime with a cleanser, eye cream, niacinamide serum, antioxidant serum, day cream and SPF 50+ in the morning and at night: cleanser, night cream and retin A serum.</p>\n<p><strong>Give us a brief list of the treatments you&#8217;ve had and loved &#8230;</strong><br />\nThe treatments I love are often the simple tactile ones: I always love a manicure and pedicure as well as a blow dry.</p>\n<img class=\"alignnone size-medium wp-image-1388\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104407/SecretsofaBeautyQueen_cover-211x300.jpg\" alt=\"\" width=\"211\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104407/SecretsofaBeautyQueen_cover-211x300.jpg 211w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104407/SecretsofaBeautyQueen_cover-768x1092.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104407/SecretsofaBeautyQueen_cover-720x1024.jpg 720w\" sizes=\"(max-width: 211px) 100vw, 211px\" />\n<p><em>Secrets of a Beauty Queen by Stephanie Darling. A damn fine read.</em></p>\n<p><strong>Any that you&#8217;ve loathed? Please explain. Personally I think some of those heat treatments are pretty ouchy!</strong><br />\nI have never loathed a treatment (because you know what they say no pain no gain) but some have been on the ouchy side. My first ultherapy treatment made my eyes water – a lot.</p>\n<p><strong>How do you feel about non-surgical procedures such as muscle freezing injections and fillers? And yes &#8211; that&#8217;s a big old Botox needle bruise you can see on my face right now.</strong><br />\nI think fillers and toxins, when delivered by an expert with a real understanding of how faces work, are genius. The big freeze is a thing of the past when administered with care and caution. For the over 50s the big news is that the revised facelift is back on the front burner.</p>\n<p><strong>Australian women are known for their beauty &#8211; why do you think that is?</strong><br />\nI think the relaxed beauty that Australian women embrace is the key – even if it has taken two hours to look that effortless.</p>\n<p><strong>I&#8217;m busy and poor. I&#8217;m kind of using that as an example but it&#8217;s totally true. Bummer. What do you recommend I do in terms of A) Skincare and B) Makeup &#8230;</strong><br />\nI think beauty supermarkets like Priceline are the best places to experiment with good affordable skin care and beauty products. You can ask advice but then take it at your own pace. And remember SPF 50+ at all times.</p>\n<p><strong>How do you feel about facials? If you can, recommend one to me.</strong><br />\nI think facials are about the feel good factor as well as results. Extractions for me are key in any facial treatment.</p>\n<p><strong>Brow tattooing or no brow tattooing?</strong><br />\nFor those of use with pale or sparse brows, expertly administered brow tattooing is a lifesaver.</p>\n<p><strong>Simplest way to revamp my look?</strong><br />\nChange up your lipstick colour. If you wear nudes embrace red.</p>\n<p><strong>Best hair tip EVER?</strong><br />\nGet a blowdry.</p>\n<p><strong>Number one way to take off 10 years?</strong><br />\nHave a facelift.</p>\n<p><strong>Do we have to spend up big if we want results?</strong><br />\nYou kinda do except if you have been good and worn sunscreen your whole life.</p>\n<p><strong>And on that, all-time favourite skincare product is</strong> &#8230;Ultraceuticals MD SPF 50+ sunscreen</p>\n<p><strong>And makeup product is</strong> &#8230;<br />\n<a href=\"https://www.shiseido.com.au/products/Makeup/Advanced_Hydro-Liquid_Compact/295/\">Shiseido Hydrop Liquid Compact.</a></p>\n<img class=\"alignnone size-medium wp-image-1397\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105413/Shiseido-HLC-300x195.jpg\" alt=\"\" width=\"300\" height=\"195\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105413/Shiseido-HLC-300x195.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105413/Shiseido-HLC.jpg 493w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><strong>Nail product?</strong><br />\n<a href=\"http://www.opi.net.au\">O.P.I Nail Polish in Lincoln Park After Dark</a>.</p>\n<img class=\"alignnone size-medium wp-image-1396\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105403/OPU-Lincoln-Park-After-Dark-177x300.jpg\" alt=\"\" width=\"177\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105403/OPU-Lincoln-Park-After-Dark-177x300.jpg 177w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105403/OPU-Lincoln-Park-After-Dark.jpg 261w\" sizes=\"(max-width: 177px) 100vw, 177px\" />\n<p>&nbsp;</p>\n<p><strong>Hair?</strong><br />\n<a href=\"http://www.tangle-teezer.com.au\">Tangle Teezer</a>.</p>\n<img class=\"alignnone size-medium wp-image-1395\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105350/tangle-teezer-254x300.jpg\" alt=\"\" width=\"254\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105350/tangle-teezer-254x300.jpg 254w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04105350/tangle-teezer.jpg 301w\" sizes=\"(max-width: 254px) 100vw, 254px\" />\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>Why thank you Mrs Darling and see you for champagne and laughter very soon.</p>\n<p>Lots of beauty love,</p>\n<p><strong>The Beauty Insider. Xx</strong></p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04104424/stephaniedarling2_photography-credit-Justin-Ridler.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"65d8cd14-fe58-5b0e-a944-d13103ff579f"}}

